import React, { Component } from "react";
import FormGroup from "@material-ui/core/FormGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { StyledLink } from "components/Util";

class SelectOptions extends Component {
  state = {
    Pharmacy: false,
    GlassStore: false,
    ALL: false,
    all: false,
    hospital: false,
    clinic: false,
    eye: false,
    search: ""
  };

  handleChange = name => event => {
    this.setState({
      ...this.state,
      [name]: event.target.checked
    });
  };

  handleChangeSearchBar = e => {
    const { value } = e.target;
    this.setState({ search: { value } });
  };

  handleSearchButton = async e => {
    //e.preventDefault();   
    this.props.onCreate(this.state);
    this.fv.value = "";
    await this.setState({
      Pharmacy: false,
      GlassStore: false,
      ALL: false,
      all: false,
      hospital: false,
      clinic: false,
      eye: false,
      search: ""
    });
  };

  render() {
    const type = this.props.type;
    return (
      <div>
        <h4>[ ]</h4>
        {type === "hospital" &&
          <FormGroup row>
            <FormControlLabel
              control={
                <Checkbox
                  checked={this.state.ALL}
                  onChange={this.handleChange("ALL")}
                  value="ALL"
                  color="primary"
                />
              }
              label=""
            />

            <FormControlLabel
              control={
                <Checkbox
                  checked={this.state.all}
                  onChange={this.handleChange("all")}
                  value="all"
                  color="primary"
                />
              }
              label=""
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={this.state.hospital}
                  onChange={this.handleChange("hospital")}
                  value="hospital"
                  color="primary"
                />
              }
              label=""
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={this.state.clinic}
                  onChange={this.handleChange("clinic")}
                  value="clinic"
                  color="primary"
                />
              }
              label=""
            />
            <FormControlLabel
              control={
                <Checkbox
                  checked={this.state.eye}
                  onChange={this.handleChange("eye")}
                  value="eye"
                  color="primary"
                />
              }
              label=""
            />
          </FormGroup>}
        {type === "store" &&
          <FormGroup row>
            <FormControlLabel
              control={
                <Checkbox
                  checked={this.state.Pharmacy}
                  onChange={this.handleChange("Pharmacy")}
                  value="Pharmacy"
                  color="primary"
                />
              }
              label=""
            />

            <FormControlLabel
              control={
                <Checkbox
                  checked={this.state.GlassStore}
                  onChange={this.handleChange("GlassStore")}
                  value="GlassStore"
                  color="primary"
                />
              }
              label=""
            />
          </FormGroup>}
        <TextField
          id="outlined-search"
          label=" "
          type="search"
          className="search"
          margin="normal"
          variant="outlined"
          inputRef={el => (this.fv = el)}
          onChange={this.handleChangeSearchBar}
        />
        <div />
        <StyledLink to="/"></StyledLink>
        <Button
          variant="contained"
          color="primary"
          onClick={this.handleSearchButton}
        >
          
        </Button>
      </div>
    );
  }
}

SelectOptions.propTypes = {};

export default SelectOptions;
