import { createAction, handleActions } from 'redux-actions'
import { pender } from 'redux-pender'
import * as AuthAPI from 'lib/api/auth'

import { Map } from 'immutable'

const CHANGE_INPUT = 'auth/CHANGE_INPUT' // input  
const INITIALIZE_FORM = 'auth/INITIALIZE_FORM' // form 
const CHECK_EMAIL_EXISTS = 'auth/CHECK_EMAIL_EXISTS' //   
const CHECK_HOSPITAL_EXISTS = 'auth/CHECK_HOSPITAL_EXISTS' //   
const CHECK_STORE_EXISTS = 'auth/CHECK_STORE_EXISTS' //   
const LOCAL_REGISTER = 'auth/LOCAL_REGISTER' //  
const LOCAL_LOGIN = 'auth/LOCAL_LOGIN' //  
const LOGOUT = 'auth/LOGOUT' // 
const SET_ERROR = 'auth/SET_ERROR' //  

export const changeInput = createAction(CHANGE_INPUT) //  { form, name, value }
export const initializeForm = createAction(INITIALIZE_FORM) // form
export const checkEmailExists = createAction(
  CHECK_EMAIL_EXISTS,
  AuthAPI.checkEmailExists
) // email
export const checkHospitalExists = createAction(
  CHECK_HOSPITAL_EXISTS,
  AuthAPI.checkHospitalExists
) // hospital
export const checkStoreExists = createAction(
  CHECK_STORE_EXISTS,
  AuthAPI.checkStoreExists
) // store
export const localRegister = createAction(
  LOCAL_REGISTER,
  AuthAPI.localRegister
) // { email, username, password }
export const localLogin = createAction(LOCAL_LOGIN, AuthAPI.localLogin) // { email, password }

export const logout = createAction(LOGOUT, AuthAPI.logout)
export const setError = createAction(SET_ERROR) // { form, message }

const initialState = Map({
  register: Map({
    form: Map({
      usertype: '',
      email: '',
      name: '',
      phonenum: '',
      password: '',
      passwordConfirm: ''
    }),
    exists: Map({
      email: false,
      hospital: false,
      store: false,
      password: false
    }),
    error: null
  }),
  login: Map({
    form: Map({
      email: '',
      password: ''
    }),
    error: null
  }),
  result: Map({})
})

export default handleActions(
  {
    [CHANGE_INPUT]: (state, action) => {
      const { form, name, value } = action.payload
      return state.setIn([form, 'form', name], value)
    },
    [INITIALIZE_FORM]: (state, action) => {
      const initialForm = initialState.get(action.payload)
      return state.set(action.payload, initialForm)
    },
    [SET_ERROR]: (state, action) => {
      const { form, message } = action.payload
      return state.setIn([form, 'error'], message)
    },
    ...pender({
      type: CHECK_EMAIL_EXISTS,
      onSuccess: (state, action) =>
        state.setIn(['register', 'exists', 'email'], action.payload.data.exists)
    }),
    ...pender({
      type: CHECK_HOSPITAL_EXISTS,
      onSuccess: (state, action) =>
        state.setIn(
          ['register', 'exists', 'hospital'],
          action.payload.data.exists
        )
    }),
    ...pender({
      type: CHECK_STORE_EXISTS,
      onSuccess: (state, action) =>
        state.setIn(['register', 'exists', 'store'], action.payload.data.exists)
    }),
    ...pender({
      type: LOCAL_LOGIN,
      onSuccess: (state, action) =>
        state.set('result', Map(action.payload.data))
    }),
    ...pender({
      type: LOCAL_REGISTER,
      onSuccess: (state, action) =>
        state.set('result', Map(action.payload.data))
    })
  },
  initialState
)
