import React, { Component } from "react";

//    .
import {
  AuthContent,
  InputWithLabel,
  AuthButton,
  RightAlignedLink,
  AuthError,
  AuthGeoloc
} from "components/Auth";

//    
import Select from "react-select";

// redux  
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as authActions from "redux/modules/auth";
import * as userActions from "redux/modules/user";

// debounce =    ,     .
//        .
import debounce from "lodash/debounce";

//     .
// this.props.coords.longitude / latitude  .
import { geolocated } from "react-geolocated";

// local storage .
import storage from "lib/storage";

//   
import { isEmail, isLength, isAlphanumeric, isNumeric } from "validator";

class Register extends Component {
  // .
  setError = message => {
    const { AuthActions } = this.props;
    AuthActions.setError({
      form: "register",
      message
    });
  };

  validate = {
    usertype: value => {
      if (!isAlphanumeric(value)) {
        this.setError("  .");
        return false;
      }
      return true;
    },
    email: value => {
      if (!isEmail(value)) {
        this.setError("   .");
        return false;
      }
      return true;
    },
    name: value => {
      if (!isLength(value, { min: 3, max: 20 })) {
        this.setError(" 3~20   .");
        return false;
      }
      return true;
    },
    phonenum: value => {
      if (
        !isNumeric(value, { no_symbols: true }) ||
        !isLength(value, { min: 11, max: 11 })
      ) {
        this.setError(" 11  . ex)01012345678");
        return false;
      }
      return true;
    },
    password: value => {
      if (!isLength(value, { min: 6 })) {
        this.setError(" 6  .");
        return false;
      }
      this.setError(null); //    null     
      return true;
    },
    passwordConfirm: value => {
      if (this.props.form.get("password") !== value) {
        this.setError("  .");
        return false;
      }
      this.setError(null);
      return true;
    }
  };

  // debounce =    ,     .
  //        .
  checkEmailExists = debounce(async email => {
    const { AuthActions } = this.props;
    try {
      await AuthActions.checkEmailExists(email);
      if (this.props.exists.get("email")) {
        this.setError("  .");
      } else {
        this.setError(null);
      }
    } catch (e) {
      console.log(e);
    }
  }, 300);

  checkHospitalExists = debounce(async name => {
    const { AuthActions } = this.props;
    try {
      await AuthActions.checkHospitalExists(name);
      console.log(this.props.exists.get("hospital"));
      if (!this.props.exists.get("hospital")) {
        this.setError("  .");
      } else {
        this.setError(null);
      }
    } catch (e) {
      console.log(e);
    }
  }, 300);

  checkStoreExists = debounce(async name => {
    const { AuthActions } = this.props;
    try {
      await AuthActions.checkStoreExists(name);
      if (!this.props.exists.get("store")) {
        this.setError("  .");
      } else {
        this.setError(null);
      }
    } catch (e) {
      console.log(e);
    }
  }, 300);

  handleSelectChange = selectedOption => {
    const { AuthActions } = this.props;
    const value = { selectedOption }.selectedOption.value;
    const name = "usertype";

    console.log("select change");
    AuthActions.changeInput({
      name,
      value,
      form: "register"
    });

    //  
    const validation = this.validate[name](value);
    if (name.indexOf("password") > -1 || !validation) return; //  ,    

    this.setError(null);
  };

  handleChange = e => {
    const { AuthActions } = this.props;
    const { name, value } = e.target;
    const { usertype, email } = this.props.form.toJS();

    console.log(usertype, email);
    AuthActions.changeInput({
      name,
      value,
      form: "register"
    });

    //  
    const validation = this.validate[name](value);
    if (name.indexOf("password") > -1 || !validation) return; //  ,    

    if (name === "email") {
      this.checkEmailExists(value);
    } else if (name === "name") {
      if (
        usertype !== "patient" &&
        usertype !== "hospital" &&
        usertype !== "store"
      ) {
        this.setError("  .");
      } else if (usertype === "hospital") {
        this.checkHospitalExists(value);
      } else if (usertype === "store") {
        this.checkStoreExists(value);
      }
    }
  };

  componentWillUnmount() {
    const { AuthActions } = this.props;
    AuthActions.initializeForm("register");
  }

  handleLocalRegister = async () => {
    const { form, AuthActions, UserActions, error, history } = this.props;
    const {
      usertype,
      email,
      name,
      phonenum,
      password,
      passwordConfirm
    } = form.toJS();
    const lng = this.props.coords.longitude;
    const lat = this.props.coords.latitude;
    const { validate } = this;

    if (error) return; //      
    if (
      !validate["usertype"](usertype) ||
      !validate["email"](email) ||
      !validate["name"](name) ||
      !validate["phonenum"](phonenum) ||
      !validate["password"](password) ||
      !validate["passwordConfirm"](passwordConfirm)
    ) {
      //    
      return;
    }

    try {
      await AuthActions.localRegister({
        usertype,
        name,
        phonenum,
        email,
        password,
        lat,
        lng
      });
      const loggedInfo = this.props.result.toJS();
      console.log(loggedInfo);
      //    (/)
      storage.set("loggedInfo", loggedInfo);
      UserActions.setLoggedInfo(loggedInfo);
      UserActions.setValidated(true);

      history.push("/"); //    
    } catch (e) {
      //  
      if (e.response.status === 409) {
        const { key } = e.response.data;
        const message = key === "email" ? "  ." : "  .";
        return this.setError(message);
      }
      this.setError("    .");
    }
  };

  render() {
    const { error } = this.props;
    const {
      email,
      name,
      phonenum,
      password,
      passwordConfirm
    } = this.props.form.toJS();
    const { handleSelectChange, handleChange, handleLocalRegister } = this;
    const options = [
      { value: "patient", label: "" },
      { value: "hospital", label: "" },
      { value: "store", label: "" }
    ];
    return (
      <AuthContent title="">
        <Select options={options} onChange={handleSelectChange} />
        <p />
        <InputWithLabel
          label=""
          name="email"
          placeholder=""
          value={email}
          onChange={handleChange}
        />
        <InputWithLabel
          label=""
          name="name"
          placeholder=""
          value={name}
          onChange={handleChange}
        />
        <InputWithLabel
          label=""
          name="phonenum"
          placeholder=""
          type="phonenum"
          value={phonenum}
          onChange={handleChange}
        />
        <InputWithLabel
          label=""
          name="password"
          placeholder=""
          type="password"
          value={password}
          onChange={handleChange}
        />
        <InputWithLabel
          label=" "
          name="passwordConfirm"
          placeholder=" "
          type="password"
          value={passwordConfirm}
          onChange={handleChange}
        />
        <AuthGeoloc {...this.props} />
        {error &&
          <AuthError>
            {error}
          </AuthError>}
        <AuthButton onClick={handleLocalRegister}></AuthButton>
        <RightAlignedLink to="/auth/login"></RightAlignedLink>
      </AuthContent>
    );
  }
}

const RootWithGeoloc = geolocated({
  positionOptions: {
    enableHighAccuracy: false
  },
  userDecisionTimeout: 5000
})(Register);

export default connect(
  state => ({
    form: state.auth.getIn(["register", "form"]),
    error: state.auth.getIn(["register", "error"]),
    exists: state.auth.getIn(["register", "exists"]),
    result: state.auth.get("result")
  }),
  dispatch => ({
    AuthActions: bindActionCreators(authActions, dispatch),
    UserActions: bindActionCreators(userActions, dispatch)
  })
)(RootWithGeoloc);
